.poster-wrapper {
	width: fit-content;
	color: white;
	background-color: rgba(0, 0, 0, 0.1);
	font-family: 'Righteous', cursive;
	text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 20px #ff0080, 0 0 30px #ff0080, 0 0 40px #ff0080,
		0 0 55px #ff0080, 0 0 75px #ff0080;
	padding: 60px 60px 30px 60px;
	text-align: center;
	-webkit-border-radius: 20px;
	-moz-border-radius: 20px;
	border: 0.2rem solid #fff;
	border-radius: 20px;
	box-shadow: 0 0 0.5rem #fff, inset 0 0 0.5rem #fff, 0 0 2rem var(--neon-border-color),
		inset 0 0 2rem var(--neon-border-color), 0 0 4rem var(--neon-border-color),
		inset 0 0 4rem var(--neon-border-color);

	hr {
		border: 1px;
		height: 3px;
		background-image: linear-gradient(
			to right,
			rgba(255, 255, 255, 0.9),
			rgba(255, 255, 255, 1),
			rgba(255, 255, 255, 0.9)
		);
		box-shadow: 0 0 7px rgba(255, 255, 255, 0.75), 0 0 10px #fff, 0 0 20px #ff0080, 0 0 30px #ff0080,
			0 0 40px #ff0080, 0 0 55px #ff0080, 0 0 75px #ff0080;
	}

	.poster-header {
		font-family: 'Vibur', cursive;
		font-size: 100px;
		margin: -48px 0 12px 0;
		// animation: flicker-text 1.5s infinite alternate;

		@keyframes flicker-text {
			0%,
			15%,
			21%,
			25%,
			40%,
			54%,
			65%,
			100% {
				text-shadow: 0 0 3px #fff, 0 0 3px #fff, 0 0 1rem var(--neon-text-color),
					0 0 2rem var(--neon-text-color), 0 0 3rem var(--neon-text-color),
					0 0 4rem var(--neon-text-color), 0 0 5rem var(--neon-text-color);
			}

			20%,
			24%,
			55% {
				text-shadow: none;
			}
		}
	}

	.poster-subheader {
		font-family: 'neoneonregular', serif;
		color: #fff;
		text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 20px #ff0080, 0 0 30px #ff0080, 0 0 40px #ff0080,
			0 0 55px #ff0080, 0 0 75px #ff0080;
		font-size: 60px;
	}
	.poster-city-caption {
		font-family: 'neoneonregular', serif;
		color: #fff;
		text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 20px #ff0080, 0 0 30px #ff0080, 0 0 40px #ff0080,
			0 0 55px #ff0080, 0 0 75px #ff0080;
		font-size: 40px;
		margin-top: 12px;
	}

	@media (max-width: 810px) {
		text-align: center;
		width: 340px !important;

		.poster-header {
			font-size: 24px !important;
		}

		.poster-subheader {
			font-size: 20px !important;
		}
	}

	@media (max-width: 540px) {
		text-align: center;
		width: 240px !important;
		padding: 30px;

		.poster-header {
			font-size: 40px !important;
			margin: -20px 0 12px 0;
		}

		.poster-subheader {
			font-size: 23px !important;
		}

		.poster-city-caption {
			font-size: 20px;
		}
	}
}
